import { useEffect, useState, useLayoutEffect } from "react";
import { logo } from "../../assets/images";
import ThemeButton from "../themeButton/themeButton";
import { MenuOutlined, CloseOutlined } from "@ant-design/icons";
import "./customNavbar.css";
import { Button } from "antd";

const MobNavbar = ({ setMobView, selected, ...props }) => {
  return (
    <div
      style={{
        flexDirection: "column",
        alignItems: "center",
        lineHeight: 2,
        top: 75,
        zIndex: 1,
        backgroundColor: "#01090e",
        paddingBottom: 15,
      }}
      className="parent navMobView2"
    >
      <a
        href="#"
        className={`${selected === "home" ? "txtGradiant" : "text-white"} bold smallText2`}
        onClick={() => {
          setMobView(false);
        }}
      >
        Home
      </a>
      <a
        href="#start"
        className={`${selected === "start" ? "txtGradiant" : "text-white"} bold smallText2`}
        onClick={() => {
          setMobView(false);
        }}
      >
        DAPP
      </a>
      <a
        href="#about"
        className={`${selected === "about" ? "txtGradiant" : "text-white"} bold smallText2`}
        onClick={() => {
          setMobView(false);
        }}
      >
        About Us
      </a>
      <a
        href="#faqs"
        className={`${selected === "faqs" ? "txtGradiant" : "text-white"} bold smallText2`}
        onClick={() => {
          setMobView(false);
        }}
      >
        FAQs
      </a>
      <a
        href="https://github.com/NovosAuditKYC/KYC/blob/main/YAAF_Miner_KYC_NOVOS.png"
        target="_blank"
        className={`text-white bold smallText2`}
      >
        KYC
      </a>
      <a href="https://watchdogs.cc/audits/2023/1/yaafminer/" target="_blank" className={`text-white bold smallText2`}>
        Audit
      </a>
      <a href="https://yaaf.net/img/yaafminer.pdf" target="_blank" className={`text-white bold smallText2`}>
        Whitepaper
      </a>
      {/* {props.children}
      <ThemeButton text={"Join Us Now"} style={{ marginTop: 10 }} /> */}
    </div>
  );
};

const CustomNavbar = ({ dappNo, aboutNo, faqNo, ...props }) => {
  const [selected, setSelected] = useState("home");
  const [mobView, setMobView] = useState(false);
  const update = () => {
    if (window.scrollY < dappNo) {
      setSelected("home");
    } else if (window.scrollY >= dappNo && window.scrollY < aboutNo) {
      setSelected("start");
    } else if (window.scrollY >= aboutNo && window.scrollY < faqNo) {
      setSelected("about");
    } else if (window.pageYOffset >= faqNo) {
      setSelected("faqs");
    }
  };
  useEffect(() => {
    window.addEventListener("scroll", update);
  }, [update]);
  return (
    <>
      <div
        style={{
          position: "sticky",
          top: 0,
          display: "flex",
          alignItems: "center",
          justifyContent: "space-between",
          zIndex: 1,
          backgroundColor: "#01090e",
        }}
        className="parent"
      >
        <img style={{ height: 80, width: 80 }} src={logo} alt="" />
        <div className="navWebView">
          <a
            href="#"
            className={`${selected === "home" ? "txtGradiant" : "text-white"} bold smallText2`}
            style={{ marginRight: 35 }}
          >
            Home
          </a>
          <a
            href="#start"
            className={`${selected === "start" ? "txtGradiant" : "text-white"} bold smallText2`}
            style={{ marginRight: 35 }}
          >
            DAPP
          </a>
          <a
            href="#about"
            className={`${selected === "about" ? "txtGradiant" : "text-white"} bold smallText2`}
            style={{ marginRight: 35 }}
          >
            About Us
          </a>
          <a
            href="#faqs"
            className={`${selected === "faqs" ? "txtGradiant" : "text-white"} bold smallText2`}
            style={{ marginRight: 35 }}
          >
            FAQs
          </a>
          <a
            href="https://github.com/NovosAuditKYC/KYC/blob/main/YAAF_Miner_KYC_NOVOS.png"
            target="_blank"
            className={`text-white bold smallText2`}
            style={{ marginRight: 35 }}
          >
            KYC
          </a>
          <a
            href="https://watchdogs.cc/audits/2023/1/yaafminer/"
            target="_blank"
            className={`text-white bold smallText2`}
            style={{ marginRight: 35 }}
          >
            Audit
          </a>
          <a
            href="https://yaaf.net/img/yaafminer.pdf"
            target="_blank"
            className={`text-white bold smallText2`}
            style={{ marginRight: 35 }}
          >
            Whitepaper
          </a>
        </div>
        <div className="navWebView">
          {/* <ThemeButton text={"Join Us Now"} /> */}
          {props.children}
        </div>
        <div className="navMobView">
          <div style={{ position: "fixed",top:25,right:80}}>
            {props.children}
          </div>

          <Button
            onClick={() => setMobView(!mobView)}
            style={{ width: 40, height: 40, display: "flex", justifyContent: "center", alignItems: "center" }}
          >
            {mobView ? <CloseOutlined style={{ fontSize: 22 }} /> : <MenuOutlined style={{ fontSize: 22 }} />}
          </Button>

        </div>
      </div>
      {mobView && <MobNavbar setMobView={e => setMobView(e)} selected={selected} setSelected={e => setSelected(e)} />}
    </>
  );
};

export default CustomNavbar;
